* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
:focus {
  outline: none;
}
a {
  text-decoration: none;
}
.app {
  overflow: hidden;
}
button {
  cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
  margin: 0;
}
